/* Content Wrap Section
--------------------------------------------------------------------------------------*/
.content {
    background: url(../../../bulded/images/25.jpg) no-repeat fixed 0px 0px /cover;
}


/* Forms Section
--------------------------------------------------------------------------------------*/
section {
    height: calc(100vh - 9.1em);
    display: flex;
    justify-content: center;
    align-items: center;

}

.forms {
    width: 40em;
    height: 20em;
    background-color: #fff;
    position: relative;

    display: flex;

    .form {
        width: 20em;
        height: 20em;
        position: absolute;
        top: 0;
        left: 0;
        font-family: sans-serif;

        flex: 1 0 0;
    }

    .signin {
        z-index: 2;
        background-color: #fff;
        
    }

    .signup {
        z-index: 1;
        background-color: #fff;
    }
}

#sign-in {
    display: grid;
    grid-template-columns: 20em;
    grid-template-rows: repeat(4, auto);
    grid-row-gap: 2em;
    
    header {
        grid-column: 1/2;
        grid-row: 1/2;
        padding-top: 2em;
        background-color: #fff;

        h3 {
            margin: 0;
            text-align: center;
        }
    }

    span:nth-of-type(1) {
        grid-column: 1/2;
        grid-row: 2/3;
        margin: 0 auto;
        perspective: 300rem;

        position: relative;

        input {
            border: none;
            outline: none;
            border-bottom: 1px solid #000;
            background-color: transparent;
        }

        label {
            position: absolute;
            left: 0;
            top: 0;
            font-size: 1em;

            transform-origin: bottom;
            transform: none;

            transition-property: transform, font-size;
            transition-duration: 0.5s, 0.3s;
            transition-delay: 0s, 0s;
            transition-timing-function: ease-in-out;
        }

        input:focus + label,
        input:valid + label {
            transform: translate3d(0px, -20px, 0px);
            font-size: 0.8em;
            font-weight: none;
        }
    }

    span:nth-of-type(2) {
        grid-column: 1/2;
        grid-row: 3/4;
        margin: 0 auto;
        perspective: 300rem;

        position: relative;

        
        input {
            border: none;
            outline: none;
            border-bottom: 1px solid #000;
            background-color: transparent;
        }

        label {
            position: absolute;
            left: 0;
            top: 0;
            font-size: 1em;

            transform-origin: top;
            transform: none;

            transition-property: transform, font-size;
            transition-duration: 0.5s, 0.3s;
            transition-delay: 0s, 0s;
            transition-timing-function: ease-out;
        }
        
        input:focus + label,
        input:valid + label {
            transform: translate3d(0px, -20px, 0px);
            font-size: 0.8em;
            font-weight: none;
        }
    }

    span:nth-of-type(3) {
        grid-column: 1/2;
        grid-row: 4/5;
        margin: 0 auto;

        button {
            width: 150px;
            padding: 5px;
            border: none;
            font-size: 1em;
            
            overflow: hidden;

            position: relative;
            z-index: 1;
            background-color: hsla(0, 0%, 0%, 0.5);
        }

        button::before {
            content: "";
            width: 100%;
            height: 30px;
            position: absolute;
            z-index: -1;
            top: 0;
            left: 0;
            background-color: hsla(0, 0%, 0%, 0.3);
    
            transform: translate3d(0px, -30px, 0px);
            transform-origin: top center;
    
            transition-property: transform;
            transition-duration: 0.3s;
            transition-delay: 0s;
            transition-timing-function: ease-out;
        }
    
        button:hover,
        button:focus {
            color: #fff;
        }
    
        button:hover:before,
        button:focus:before {
            transform: translate3d(0px, 0px, 0px);
        }
    }
}

