.overlay-container {
    width: 50%;
    height: 100%;
    position: absolute;
    right: 0;
    z-index: 5;
    flex: 1 0 0;
    overflow: hidden;
    background-color: lightgreen;
}

.overlay-photo {
    width: 200%;
    height: 100%;
    position: absolute;
    top: 0;
    left: -100%;
    z-index: 4;
    transform-origin: left center;
